import { pl } from 'date-fns/locale';
import formatLong from 'date-fns/locale/pl/_lib/formatLong/index';

export const months = [
  'Stycze',
  'Luty',
  'Marzec',
  'Kwiecie',
  'Maj',
  'Czerwiec',
  'Lipiec',
  'Sierpie',
  'Wrzesie',
  'Padziernik',
  'Listopad',
  'Grudzie'
];

export const monthsShort = [
  'Sty',
  'Lut',
  'Mar',
  'Kwi',
  'Maj',
  'Cze',
  'Lip',
  'Sie',
  'Wrz',
  'Pa',
  'Lis',
  'Gru'
];

export const days = [
  'Niedziela',
  'Poniedziaek',
  'Wtorek',
  'roda',
  'Czwartek',
  'Pitek',
  'Sobota'
];
export const daysShort = ['Nd', 'Pn', 'Wt', 'r', 'Cz', 'Pt', 'So'];

export const locale = {
  pl,
  localize: {
    day: (n) => daysShort[n],
    month: (n) => months[n]
  },
  options: {
    weekStartsOn: 1
  },
  formatLong
};
